/* eslint-disable import/no-extraneous-dependencies */
import React from 'react';
import { fade } from '@material-ui/core/styles/colorManipulator';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Paper,
  Button,
  InputBase,
  SearchIcon,
  withStyles,
} from '../../materialLib';
import Link from '../../components/Link/Link';
import { COLORS } from '../../constants';

const styles = theme => ({
  root: {
    width: '100%',
    marginTop: theme.spacing.unit * 3,
    overflowX: 'auto',
  },
  table: {
    minWidth: 700,
  },
  customBtn: {
    backgroundColor: COLORS.NAVY_BLUE,
    margin: theme.spacing.unit,
    textTransform: 'capitalize',
  },
  search: {
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(COLORS.NAVY_BLUE, 0.1),
    '&:hover': {
      backgroundColor: fade(COLORS.NAVY_BLUE, 0.1),
    },
    marginLeft: '0 !important',
    width: '22% !important',
    [theme.breakpoints.up('sm')]: {
      marginLeft: theme.spacing.unit * 3,
      width: 'auto',
    },
    float: 'left',
  },
  searchIcon: {
    width: theme.spacing.unit * 9,
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    color: COLORS.WHITE,
    backgroundColor: COLORS.NAVY_BLUE,
    borderRadius: `${theme.shape.borderRadius}px 0 0 ${
      theme.shape.borderRadius
    }px`,
  },
  inputRoot: {
    color: 'inherit',
    width: '100%',
  },
  inputInput: {
    paddingTop: theme.spacing.unit,
    paddingRight: theme.spacing.unit,
    paddingBottom: theme.spacing.unit,
    paddingLeft: theme.spacing.unit * 10,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('md')]: {
      width: 200,
    },
  },
});

function Groups(props) {
  const { classes, groups, handleChangeSearch } = props;
  return (
    <>
      <div>
        <div className={classes.search}>
          <div className={classes.searchIcon}>
            <SearchIcon />
          </div>
          <InputBase
            placeholder="Search a group"
            classes={{
              root: classes.inputRoot,
              input: classes.inputInput,
            }}
            onChange={e => {
              handleChangeSearch(e, 'groups', 'getGroups');
            }}
            autoFocus
          />
        </div>
        <Link to="/groups-create">
          <Button
            variant="contained"
            size="small"
            color="primary"
            className={classes.customBtn}
            style={{ float: 'right' }}
          >
            Add Group
          </Button>
        </Link>
      </div>
      <Paper className={classes.root}>
        <Table className={classes.table}>
          <TableHead>
            <TableRow>
              <TableCell>Key</TableCell>
              <TableCell align="left">Name</TableCell>
              <TableCell align="left">Created By</TableCell>
              <TableCell align="left">Actions</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {groups.map(row => (
              <TableRow key={row.keys.key}>
                <TableCell component="th" scope="row">
                  {row.keys.key}
                </TableCell>
                <TableCell align="left">{row.name}</TableCell>
                <TableCell align="left">{row.createdBy.key}</TableCell>
                <TableCell align="left">
                  <Link
                    style={{ textDecoration: 'none' }}
                    to={`/groups-update/${row.keys.key}`}
                  >
                    <Button
                      variant="contained"
                      size="small"
                      className={classes.customBtn}
                      color="primary"
                      style={{ margin: 0 }}
                    >
                      Update
                    </Button>
                  </Link>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </Paper>
    </>
  );
}

export default withStyles(styles)(Groups);
